import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import NewClient from "./NewClient";
import Client from "./Client";
import Alphabet from "../templates/Alphabet";

class Clients extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      categories: null,
      overlayHiden: true,
      letter: null
    };
  }
  selectLetter = atr => {
    if (this.refs.picked != null) {
      this.refs.picked.className = "";
      this.refs = {
        picked: ""
      };
    }
    if (this.state.letter === atr.target.letter) {
      this.setState({
        letter: null
      });
    }
    else {
      this.state.letter = atr.target.innerHTML;
      atr.target.className = "liPicked";
      this.refs = {
        picked: atr.target.parentNode
      };
    }
    fetch(
      "http://localhost:8080/api/clients/first-letter?letter=" +
        this.state.letter
    ) 
      .then(res => res.json())
      .then(
        result => {
          this.setState({
            isLoaded: true,
            clients: result.clients
          });
        },
        error => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      );
  };

  toggleOverlay = () => {
    this.setState({
      overlayHiden: !this.state.overlayHiden
    });
  };
  search = () => {
    if (this.refs.search.value !== null && this.refs.search.value !== "") {
      fetch(
        "http://localhost:8080/api/clients/by-name?name=" +
          this.refs.search.value
      )
        .then(res => res.json())
        .then(
          result => {
            this.setState({
              isLoaded: true,
              clients: result.clients
            });
          },
          error => {
            this.setState({
              isLoaded: true,
              error
            });
          }
        );
    } else {
      this.componentDidMount();
    }
  };

  componentDidMount() {
    fetch("http://localhost:8080/api/clients")
      .then(res => res.json())
      .then(
        result => {
          this.setState({
            isLoaded: true,
            clients: result.clients
          });
        },
        error => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      );
  }

  render() {
    const { error, isLoaded, clients } = this.state;
    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
      return <div>Loading...</div>;
    } else {
      return (
        <div className="content">
          {!this.state.overlayHiden && (
            <NewClient toggleOverlay={this.toggleOverlay} />
          )}
          <div className="new">
            <p className=" inline" onClick={this.toggleOverlay}>
              Create new Client
            </p>
            <input
              className="search"
              ref="search"
              onChange={this.search}
              placeholder="Search"
            />
          </div>
          <Alphabet selectLetter={this.selectLetter} />
          <div>
            {clients.map(item => (
              <Client key={item.id} item={item} />
            ))}
          </div>
        </div>
      );
    }
  }
}

export default Clients;
