import React from "react";

class NewClient extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      categories: null,
      overlayHiden: true
    };
  }

  addTeamMember = () => {
    var countryId = this.refs.editCountry.options[
      this.refs.editCountry.selectedIndex
    ].value;

    fetch("http://localhost:8080/api/clients", {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        name: this.refs.editName.value,
        address: this.refs.editAddress.value,
        city: this.refs.editCity.value,
        zipCode: this.refs.editZipCode.value,
        country: {
          id: countryId
        }
      })
    });
    window.location.reload();
  };

  componentDidMount() {
    fetch("http://localhost:8080/api/countries")
      .then(res => res.json())
      .then(
        result => {
          this.setState({
            isLoaded: true,
            countries: result.countries
          });
        },
        error => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      );
  }

  render() {
    const { error, isLoaded} = this.state;
    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
      return <div>Loading...</div>;
    } else {
      return (
        <div id="overlay">
          <div className="form-group overlay-From">
            <form className="mx-auto inputDiv">
              <h5>Create new team member</h5>
              <div className="form-group">
                <label>Name</label>
                <input
                  className="form-control"
                  ref="editName"
                  placeholder="Name"
                />
              </div>
              <div className="form-group">
                <label>Address</label>
                <input
                  className="form-control"
                  ref="editAddress"
                  placeholder="Address"
                />
              </div>
              <div className="form-group">
                <label>City</label>
                <input
                  className="form-control"
                  ref="editCity"
                  placeholder="City"
                />
              </div>
              <div className="form-group">
                <label>Zip code</label>
                <input
                  className="form-control"
                  ref="editZipCode"
                  placeholder="Zip Code"
                />
              </div>
              <div className="form-group">
                <label className="radioLabel">Country</label>
                <select ref="editCountry">
                  {this.state.countries.map(item => (
                    <option value={item.id}>{item.name}</option>
                  ))}
                </select>
              </div>
              <button
                onClick={this.addTeamMember}
                type="button"
                className="btn btn-success overlaybtn"
              >
                Add
              </button>
              <button
                onClick={this.props.toggleOverlay}
                type="button"
                className="btn btn-danger overlaybtn"
              >
                Cancel
              </button>
            </form>
          </div>
        </div>
      );
    }
  }
}
export default NewClient;
