import React from "react";
import logo from "../logo.png";
import Login from "./Login";

class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      overlayHiden: true,
      username: null,
      error: null,
      isLoaded: false,
      user: null
    };
  }

  componentDidMount() {
    fetch("http://localhost:8080/api/auth/loggedIn")
      .then(res => res.json())
      .then(
        result => {
          this.setState({
            isLoaded: true,
            username: result.username
          });
        },
        error => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      );
  }

  logout = () => {
    fetch("http://localhost:8080/api/auth/logout", {
      method: "POST"
    })
      .then(res => res.json())
      .then(
        result => {
          this.setState({
            isLoaded: true,
            username: result.username
          });
        },
        error => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      );
    window.location.reload();
  };

  toggleOverlay = () => {
    this.setState({
      overlayHiden: !this.state.overlayHiden
    });
  };

  render() {
    return (
      <header>
        {!this.state.overlayHiden && (
          <Login toggleOverlay={this.toggleOverlay} />
        )}
        {(this.state.username === null ||
          this.state.username === undefined ||
          this.state.username === "anonymousUser") && (
          <div>
            <button
              type="button"
              onClick={this.toggleOverlay}
              className="btn btn-light headerButton"
            >
              Login
            </button>
          </div>
        )}

        {this.state.username !== null &&
          this.state.username !== undefined &&
          this.state.username !== "anonymousUser" && (
            <div>
              <p>{this.state.username}</p>
              <p onClick={this.logout}> | Logout</p>
            </div>
          )}
        <img className="logo" src={logo} alt="logo" />

        <a href="./time-stamp">
          <button type="button" className="btn btn-light headerButton">
            TimeStamp
          </button>
        </a>

        <a href="./clients">
          <button type="button" className="btn btn-light headerButton">
            Clients
          </button>
        </a>
        <a href="./projects">
          <button type="button" className="btn btn-light headerButton">
            Projects
          </button>
        </a>
        <a href="./categories">
          <button type="button" className="btn btn-light headerButton">
            Categories
          </button>
        </a>
        <a href="./team-members">
          <button type="button" className="btn btn-light headerButton">
            Team members
          </button>
        </a>
        <a href="./reports">
          <button type="button" className="btn btn-light headerButton">
            Reports
          </button>
        </a>
      </header>
    );
  }
}

export default Header;
