import React from "react";
class Alphabet extends React.Component {
  render() {
    return (
      <div>
        <ul className="letters" onClick={this.props.selectLetter}>
          <li>a</li>
          <li>b</li>
          <li>c</li>
          <li>d</li>
          <li>e</li>
          <li>f</li>
          <li>g</li>
          <li>h</li>
          <li>i</li>
          <li>j</li>
          <li>k</li>
          <li>l</li>
          <li>m</li>
          <li>n</li>
          <li>o</li>
          <li>p</li>
          <li>q</li>
          <li>r</li>
          <li>s</li>
          <li>t</li>
          <li>u</li>
          <li>v</li>
          <li>w</li>
          <li>x</li>
          <li>y</li>
          <li>z</li>
        </ul>
      </div>
    );
  }
}

export default Alphabet;
