import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";

class EditProject extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      teamMembers: null,
      clients: null,
      overlayHiden: true
    };
  }

  componentDidMount() {
    fetch("http://localhost:8080/api/team-members")
      .then(res => res.json())
      .then(
        result => {
          this.setState({
            isLoaded: true,
            teamMembers: result.teamMembers
          });
        },
        error => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      );

    fetch("http://localhost:8080/api/clients")
      .then(res => res.json())
      .then(
        result => {
          this.setState({
            isLoadedClietns: true,
            clients: result.clients
          });
        },
        error => {
          this.setState({
            isLoadedClietns: true,
            error
          });
        }
      );
  }
  save = () => {
    var leadId = this.refs.editLead.options[this.refs.editLead.selectedIndex]
      .value;
    var clientId = this.refs.editClient.options[
      this.refs.editClient.selectedIndex
    ].value;

    fetch("http://localhost:8080/api/projects", {
      method: "PUT",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        id: this.props.item.id,
        name: this.refs.editName.value,
        description: this.refs.editDesc.value,
        lead: {
          id: leadId
        },
        client: {
          id: clientId
        }
      })
    });
    window.location.reload();
  };

  delete = () => {
    fetch("http://localhost:8080/api/projects/" + this.props.item.id, {
      method: "DELETE",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      },
      body: JSON.stringify({})
    });
    window.location.reload();
  };

  render() {
    const { error, isLoaded, isLoadedClietns } = this.state;
    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded || !isLoadedClietns) {
      return <div>Loading...</div>;
    } else {
      return (
        <div className="form-group overlay-From">
          <form className="mx-auto inputDiv">
            <h5>Create new project</h5>
            <div className="form-group">
              <label>Name</label>
              <input
                defaultValue={this.props.item.name}
                className="form-control"
                ref="editName"
                placeholder="Name"
              />
            </div>
            <div className="form-group">
              <label>Description</label>
              <input
                defaultValue={this.props.item.description}
                className="form-control"
                ref="editDesc"
                placeholder="Description"
              />
            </div>
            <div className="form-group">
              <label className="radioLabel">Lead</label>
              <select ref="editLead">
                {this.state.teamMembers.map(item => (
                  <option value={item.id}>{item.name}</option>
                ))}
              </select>
            </div>
            <div className="form-group">
              <label className="radioLabel">Clients</label>
              <select ref="editClient">
                {this.state.clients.map(item => (
                  <option value={item.id}>{item.name}</option>
                ))}
              </select>
            </div>

            <button
              onClick={this.save}
              type="button"
              className="btn btn-success overlaybtn"
            >
              Save
            </button>
            <button
              onClick={this.delete}
              type="button"
              className="btn btn-danger overlaybtn"
            >
              Delete
            </button>
          </form>
        </div>
      );
    }
  }
}
export default EditProject;
