import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";

class EditClient extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      categories: null,
      overlayHiden: true
    };
  }
  componentDidMount() {
    fetch("http://localhost:8080/api/countries")
      .then(res => res.json())
      .then(
        result => {
          this.setState({
            isLoaded: true,
            countries: result.countries
          });
        },
        error => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      );
  }
  save = () => {
    var countryId = this.refs.editCountry.options[
      this.refs.editCountry.selectedIndex
    ].value;

    fetch("http://localhost:8080/api/clients", {
      method: "PUT",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        id: this.props.item.id,
        name: this.refs.editName.value,
        address: this.refs.editAddress.value,
        city: this.refs.editCity.value,
        zipCode: this.refs.editZipCode.value,
        country: {
          id: countryId
        }
      })
    });
    window.location.reload();
  };

  delete = () => {
    fetch("http://localhost:8080/api/clients/" + this.props.item.id, {
      method: "DELETE",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      },
      body: JSON.stringify({})
    });
    window.location.reload();
  };

  render() {
    const { error, isLoaded } = this.state;
    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
      return <div>Loading...</div>;
    } else {
      return (
        <div className="form-group overlay-From">
          <form className="mx-auto inputDiv">
            <h5>Create new team member</h5>
            <div className="form-group">
              <label>Name</label>
              <input
                defaultValue={this.props.item.name}
                className="form-control"
                ref="editName"
                placeholder="Name"
              />
            </div>
            <div className="form-group">
              <label>Address</label>
              <input
                defaultValue={this.props.item.address}
                className="form-control"
                ref="editAddress"
                placeholder="Address"
              />
            </div>
            <div className="form-group">
              <label>City</label>
              <input
                defaultValue={this.props.item.city}
                className="form-control"
                ref="editCity"
                placeholder="City"
              />
            </div>
            <div className="form-group">
              <label>Zip code</label>
              <input
                defaultValue={this.props.item.zipCode}
                className="form-control"
                ref="editZipCode"
                placeholder="Zip Code"
              />
            </div>
            <div className="form-group">
              <label className="radioLabel">Country</label>
              <select ref="editCountry">
                {this.state.countries.map(item => (
                  <option
                    value={item.id}
                    selected={item.id === this.props.item.country.id}
                  >
                    {item.name}
                  </option>
                ))}
              </select>
            </div>
            <button
              onClick={this.save}
              type="button"
              className="btn btn-success overlaybtn"
            >
              Save
            </button>
            <button
              onClick={this.delete}
              type="button"
              className="btn btn-danger overlaybtn"
            >
              Delete
            </button>
          </form>
        </div>
      );
    }
  }
}
export default EditClient;
