import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import NewProject from "./NewProject";
import Project from "./Project";
import Alphabet from "../templates/Alphabet";

class Projects extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      projects: null,
      overlayHiden: true,
      letter: null
    };
  }
  selectLetter = atr => {
    if (this.refs.picked != null) {
      this.refs.picked.className = "";
      this.refs = {
        picked: ""
      };
    }
    if (this.state.letter === atr.target.letter) this.state.letter = null;
    else {
      this.state.letter = atr.target.innerHTML;
      atr.target.parentNode.className = "liPicked";
      this.refs = {
        picked: atr.target.parentNode
      };
    }
    fetch(
      "http://localhost:8080/api/projects/first-letter?letter=" +
        this.state.letter
    )
      .then(res => res.json())
      .then(
        result => {
          this.setState({
            isLoaded: true,
            projects: result.projects
          });
        },
        error => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      );
  };

  toggleOverlay = () => {
    this.setState({
      overlayHiden: !this.state.overlayHiden
    });
  };

  search = () => {
    if (this.refs.search.value !== null && this.refs.search.value !== "") {
      fetch(
        "http://localhost:8080/api/projects/by-name?name=" +
          this.refs.search.value
      )
        .then(res => res.json())
        .then(
          result => {
            this.setState({
              isLoaded: true,
              projects: result.projects
            });
          },
          error => {
            this.setState({
              isLoaded: true,
              error
            });
          }
        );
    } else {
      this.componentDidMount();
    }
  };
  componentDidMount() {
    fetch("http://localhost:8080/api/projects")
      .then(res => res.json())
      .then(
        result => {
          this.setState({
            isLoaded: true,
            projects: result.projects
          });
        },
        error => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      );
  }

  render() {
    const { error, isLoaded, projects } = this.state;
    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
      return <div>Loading...</div>;
    } else {
      return (
        <div className="content">
          {!this.state.overlayHiden && (
            <NewProject toggleOverlay={this.toggleOverlay} />
          )}
          <div className="new">
            <p className=" inline" onClick={this.toggleOverlay}>
              Create new Client
            </p>
            <input
              className="search"
              ref="search"
              onChange={this.search}
              placeholder="Search"
            />
          </div>
          <Alphabet selectLetter={this.selectLetter} />
          <div>
            {projects.map(item => (
              <Project key={item.id} item={item} />
            ))}
          </div>
        </div>
      );
    }
  }
}

export default Projects;
